@import url(https://fonts.googleapis.com/css2?family=PT+Sans:ital,wght@0,400;0,700;1,400;1,700&display=swap);
.chat-message {
  margin: 1rem 0;
  background: white;
  border-radius: 0.25rem;
  position: relative;
  padding: 1rem;
  background: hsl(293, 100%, 26%);
  color: white;
}

.chat-message:nth-child(even) {
  margin-right: 2rem;
}

.chat-message:nth-child(odd) {
  margin-left: 2rem;
}

.chat-message__name {
  font-weight: bold;
  font-size: 0.75rem;
}

.chat-message__date {
  margin-left: 0.25rem;
  font-size: 0.5rem;
}

.speak-button {
    display: block;
    padding: 0.5rem 1rem;
    margin: 0.2rem;
    font-size: 0.9rem;
    background: hsl(204, 99%, 37%);
    color: white;
    outline: none;
    border: 3px dashed transparent;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .speak-button:hover {
    background: hsl(204, 100%, 31%);
    transform: rotate(1deg);
  }
  
  .speak-button:active {
    background: hsl(204, 100%, 24%);
    transform: rotate(2deg);
  }
  
  .speak-button:focus {
    border: 3px dashed black;
  }
.welcome-message{
font-style: italic;
-webkit-text-decoration: wavy;
        text-decoration: wavy;
color: rgb(209, 58, 88);

}
.social-message {
    margin: 1rem 0;
    background: rgb(190, 120, 120);
    border-radius: 0.2rem;
    position:relative;
    border-style: solid;
    padding: 1rem;
    background: hsl(66, 90%, 92%);
    color: rgb(87, 77, 77);
  }
  
  .social-message:nth-child(even) {
    margin: 2rem;
  }
  
  .social-message:nth-child(odd) {
    margin: 2rem;
  }
  
  .social-message__name {
    font-weight: bold;
    font-size: 0.65rem;
  }
  
  .social-message__date {
    margin-left: 0.40rem;
    font-size: 0.5rem;
  }
  .social-message__numLikes{
    font: bold 11px Arial;
    text-decoration: none;
    background-color: #EEEEEE;
    color: #000000;
    padding: 2px 6px 2px 6px;
    border-top: 1px solid #CCCCCC;
    border-right: 1px solid #333333;
    border-bottom: 1px solid #333333;
    border-left: 1px solid #CCCCCC;
  }
  .social-message__numThumbsUp{
    font: bold 11px Arial;
    text-decoration: none;
    background-color: #EEEEEE;
    color: #eb5e5e;
    padding: 2px 6px 2px 6px;
    border-top: 1px solid #CCCCCC;
    border-right: 1px solid #333333;
    border-bottom: 1px solid #333333;
    border-left: 1px solid #CCCCCC;
  }
  .social-message__numSparkles{
    font: bold 11px Arial;
    text-decoration: none;
    background-color: #EEEEEE;
    color: #7ce4b0;
    padding: 2px 6px 2px 6px;
    border-top: 1px solid #CCCCCC;
    border-right: 1px solid #333333;
    border-bottom: 1px solid #333333;
    border-left: 1px solid #CCCCCC;
  }
.end-screen {
    text-align: center;
  }
  
  .end-screen__trophy {
    font-size: 2.5rem;
    margin: 1rem;
  }
  
  .end-screen__stat {
    margin: 1rem;
  }
  
  .end-screen__stat-label {
    font-size: 1rem;
    font-style: italic;
  }
  
  .end-screen__stat-value {
    font-size: 2rem;
    font-weight: bold;
  }
  
  .end-screen__button {
    padding: 0.8rem 1.8rem;
    font-size: 1rem;
  }
.stats {
    display: flex;
    justify-content: center;
    list-style: none;
    padding: 0;
    margin: 0 0 1rem 0;
  }
  
  .stats__stat-container {
    min-width: 100px;
    text-align: center;
    color: white;
    background: #000067;
    padding: 0.4rem 0.6rem;
    border-radius: 0.5rem;
    margin: 0 0.25rem;
  }
  
  .stats__stat-label {
    font-size: 0.6rem;
    font-style: italic;
  }
  
  .stats__stat-value {
    font-size: 1rem;
  }
.trivia-item__question {
    text-align: center;
    font-size: 2rem;
  }
.trivia-item__diffy {

  text-align: left;
  font-size: medium;
}
  
  .trivia-item__answers {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    list-style: none;
    padding: 0;
    margin: 0;
    grid-gap: 1rem;
    gap: 1rem;
  }
  
  .trivia-item__button {
    display: block;
    width: 100%;
    height: 100%;
    padding: 1.5rem 1.5rem;
    cursor: pointer;
    background: #6060ff;
    color: white;
    font-size: 1.4rem;
    border-radius: 1rem;
    outline: none;
    border: 3px solid transparent;
    transition: background 0.15s, transform 0.15s, border-color 0.15s;
  }
  
  .trivia-item__next-button {
    margin: 1rem auto;
    width: 25%;
  }
  
  .trivia-item__button:hover {
    background: #3c3cda;
    transform: rotate(1deg) scale(1.01);
  }
  
  .trivia-item__button:active {
    background: #2828a3;
    transform: rotate(2deg) scale(1.02);
  }
  
  .trivia-item__button:focus {
    border-color: black;
  }
  
  .trivia-item__button.trivia-item__button--disabled {
    background: #c3c3d8;
  }
  
  .trivia-item__button.trivia-item__button--correct {
    background: #27bd26;
  }
  
  .trivia-item__button.trivia-item__button--incorrect {
    background: #c02626;
  }
.list{
    list-style: none;
    }
    
    .list li::before{
       content: "*";
        padding-right: 0.5rem;
    }
.page-header {

    margin-bottom: 2rem;
    text-align: center;
}

.page-header__logo {

    font-size: 2rem;
}

.page-header__nav {

    font-size: 1.25rem;
}
html {
  /* Create a base font size that is inherited for elements on the page. */
  font-size: 22px;
  /* Ditto for the font-family. */
  font-family: "PT Sans", sans-serif;
}

/* Force all elements to inherit the font-family. Some elements (like button) don't inherit by
default. */
* {
  font-family: inherit;
}

body {
  background: url(/static/media/memphis-colorful-dim.a1c8ddcc.png);
  color: rgb(4, 0, 17);
  max-width: 800px;
  /* This margin auto trick centers block elements. */
  margin: 0 auto;
}

/* New general CSS in this video: */

button {
  padding: 0.5rem 1rem;
  font-size: 0.75rem;
}
button {
  background: hsl(204, 99%, 37%);
